/* -------------------------------------------------------------------------------
    A. Common CSS
--------------------------------------------------------------------------------*/
::-moz-selection {
  color: #fff;
  background: #ffa200;
}

::selection {
  color: #fff;
  background: #ffa200;
}

html, body {
  height: 100%;
  font-family: Roboto, Arial, sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  font-family: Montserrat, Arial, sans-serif;
}

a, button {
  transition: all 0.25s ease-in-out;
}

a:focus,
a:active, button:focus,
button:active {
  outline: none !important;
}

a {
  color: #333;
}

a:hover,
a:focus {
  color: #ffa200;
  text-decoration: none;
}

label {
  font-size: 12px;
  font-family: Montserrat, Arial, sans-serif;
}

label sup {
  color: red;
}

img {
  max-width: 100%;
  height: auto;
}

.btn,
.search-submit,
.alert,
.tooltip-inner,
.form-control {
  border-radius: 0;
}

/* Color helpers */
.white {
  color: #fff !important;
}

.dark {
  color: #333 !important;
}

.gray {
  color: #898989 !important;
}

/* Bg hepers */
.bg-lighten {
  background-color: #f9f9f9 !important;
}

.bg-gray {
  background-color: #f4f4f4 !important;
}

.bg-dark {
  background-color: #333 !important;
}

.bg-darken {
  background-color: #262626 !important;
}

/* Font size */
.tiny {
  font-size: 10px;
}

.small {
  font-size: 12px;
}

.medium {
  font-size: 14px;
}

.large {
  font-size: 16px;
}

/**/
/* Font weight helpers */
.light {
  font-weight: 300;
}

.normal {
  font-weight: 400;
}

.bold {
  font-weight: 700;
}

/* Text helpers */
.center {
  text-align: center;
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.upper,
.text-upper {
  text-transform: uppercase;
}

.spacing-5 {
  letter-spacing: 5px;
}

.spacing-10 {
  letter-spacing: 10px;
}

/* Display helpers */
.block {
  display: block !important;
}

.inline-block {
  display: inline-block !important;
}

.inline {
  display: inline !important;
}

/* Border helpers */
.border-left {
  border-left: solid 1px #e1e1e1;
}

.border-right {
  border-right: solid 1px #e1e1e1;
}

.border-top {
  border-top: solid 1px #e1e1e1;
}

.border-bottom {
  border-bottom: solid 1px #e1e1e1;
}

/* Fullpage */
.fp-table {
  display: table;
  table-layout: fixed;
  text-align: center;
}

.fp-table.text-left {
  text-align: left;
}

.fp-table,
.fp-table-cell {
  width: 100%;
  height: 100%;
}

.fp-table-cell {
  display: table-cell;
  vertical-align: middle;
}

/* Col centered */
.col-centered {
  float: none;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/* Col border */
@media (min-width: 769px) {
  .cols-border > [class*='col-'] {
    border-right: solid 1px #e1e1e1;
  }
  .cols-border > [class*='col-']:last-child {
    border-right: transparent;
  }
}

/* No gutter */
.no-gutter {
  margin-left: 0;
  margin-right: 0;
}

.no-gutter > [class*='col-'] {
  padding-right: 0;
  padding-left: 0;
}

/* White pop-up */
.white-popup {
  position: relative;
  background: #fff;
  padding: 20px;
  width: auto;
  max-width: 500px;
  margin: 20px auto;
}

/* Embed 1:1 */
.embed-responsive-1by1 {
  padding-bottom: 100%;
}

.underline:after {
  content: '';
  display: block;
  width: 75px;
  height: 0;
  margin: 15px auto 0 auto;
  border-bottom: solid 3px #333;
}

