/** Subscible **/
.subscible-wrapper {
    color: #fff;
    background: #2F2F2F;
}

.subscribe-title {
    margin: 0;
    text-transform: uppercase;
}

.subscribe-comment {
    margin-bottom: 0;

    p:last-child {
        margin-bottom: 0;
    }
}

.subscible-form {
    .form-control {
        border: none;
        height: 38px;
        padding-top: 10px;
    }
}

// Inline
.subscible-inline {
    padding: 35px;

    .subscribe-title {
        font-size: 22px;
        line-height: 22px;
    }

    .subscribe-comment {
        margin-bottom: 15px;
    }

    .subscible-form {
        .form-group,
        .form-submit {
            float: left;
        }

        .form-group {
            width: 65%;
            padding-right: 10px;
            margin-bottom: 0;
        }

        .form-submit {
            width: 35%;
            .btn {
                width: 100%;
                padding-left: 0;
                padding-right: 0;
            }
        }
    }

    @include media('>=md') {
        .subscribe-comment {
            text-align: center;
            margin-bottom: 0;
        }
    }

    @include media('<=sm') {
        .subscribe-title {
            margin-bottom: 15px !important;
        }

        .form-group,
        .form-submit {
            float: none !important;
            width: 100% !important;
            padding: 0 !important;
        }

        .form-group {
            margin-bottom: 10px !important;
        }

        .form-submit .btn {
            padding-left: 15px !important;
            padding-right: 15px !important;
            width: auto !important;
        }
    }
}

// Block
.subscible-block {
    padding: 20px;

    .subscribe-title {
        font-size: 16px;
        margin-bottom: 10px;
    }

    .subscribe-comment {
        color: #acacac;
        font-size: 12px;
        margin-bottom: 20px;
    }
}

.footer-wrapper {
    .subscrible-form {
        .form-control {
            border: none;
        }
    }
}
