// Main
#wrapper {
    background: #fff;
}

#main {
    display: block;
    margin-bottom: 50px;
}

#menu-panel {
    color: #fff;
    background: #333;
}

.products-slick {
    @include media('<lg') {
        .slick-prev,
        .slick-next {
            color: #fff;
            background: rgba(0,0,0,.25);
            height: 50px;
            width: 25px;
            margin-top: -100px;

            &:before,
            &:after {
                color: #fff;
            }
        }
        .slick-next {
            right: 15px;
        }
        .slick-prev {
            left: 15px;
        }
    }
}
