.main-slider-wrapper {
    color: #fff;
    position: relative;

    @include media('<=xs') {
        display: none;
    }
}

.main-slider-item {
    position: relative;

    .container {
        position: relative;
        z-index: 1000;
    }
}

.main-slider-image {
    img {
        width: 100%;
    }
}

.main-slider-text {
    color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    h3 {
        font-size: 25px;
        margin-top: 0;
        text-transform: uppercase;

        &.small {
            font-size: 20px;
        }

        &.light {
            font-family: $font-family-sans-serif;
            font-size: 30px;
            font-weight: 300;
        }
    }

    h2 {
        font-size: 80px;
        margin-bottom: 50px;
        text-transform: uppercase;

        &.small {
            font-size: 60px;
        }
    }
}

@include media('<=md') {
    .main-slider-text {
        h2 {
            font-size: 30px;
            margin-bottom: 15px;

            &.small {
                font-size: 20px;
            }
        }

        .button {

        }
    }
}

@include media('<=sm') {
    .main-slider-wrapper {
        .owl-dots {
            display: none;
        }

        .main-slider-text {
            h2 {
                font-size: 20px;
                margin-bottom: 15px;

                &.small {
                    font-size: 15px;
                }
            }

            .button .btn {
                padding: 7px 10px;
            }
        }
    }
}
