.awe-nav {
    display: block;
    text-align: center;
    padding-left: 0;
    border-bottom: solid 1px $border-color;
    margin-bottom: 0;

    > li {
        display: inline-block;

        > a {
            font-family: $font-family-heading;
            padding: 20px 15px;
            font-size: 14px;
            text-transform: uppercase;
            display: inline-block;

            position: relative;
            bottom: -1px;
            border-bottom: solid 2px transparent;
        }

        &.current,
        &.active > a {
            color: $hover-color;
            border-bottom-color: #ffa200;
        }
    }
}

.awe-nav-responsive {
    overflow: auto;

    .awe-nav {
        white-space: nowrap;
    }
}
