.main-header {
    color: #fff;
    background-color: #333;
    text-align: center;
    padding: 60px 0;

    &-small {
        padding: 30px 0;
    }

    &-large {
        padding: 100px 0;
    }

    h1,
    &-title {
        color: #fff;
        font-size: 34px;
        text-transform: uppercase;
        margin-top: 0;
        margin-bottom: 0;
        letter-spacing: 10px;
        text-indent: center;

        small {
            color: #fff;
            font-size: 18px;
            letter-spacing: 3px;
        }

        &.block small {
            display: block;
        }
    }

    &-light {
        color: #333;
        background: #f9f9f9;

        h1,
        &-title {
            color: #333;
        }
    }

    .breadcrumb {
        margin-bottom: 15px;
    }
}

.section-header {
    margin-bottom: 50px;
    @include clearfix();

    h2 {
        margin-top: 0;
        margin-bottom: 0;
        font-size: 40px;
        text-transform: uppercase;

        &.medium {
            font-size: 30px;
        }
    }

    p {
        color: $gray-color;
        font-size: 16px;
        margin-bottom: 0;

        &.medium {
            font-size: 14px;
        }
    }
}
